.dropbtn {
    background-color: #FBB931;
    color: rgb(0, 0, 0);
    width: 63px;
    height: 29px;
    font-size: 13px;
    border: none;
    cursor: pointer;
    border-radius: 35px;
    align-items: center;
    text-decoration: none;
}

.dropdown {
    display: inline-block;
    margin-right: 48px;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

.dropdown-content link {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    font-size: 10px;
}

.dropdown-content link:hover {
    background-color: #f1f1f1;
    text-decoration: none
}

.dropdown:hover .dropdown-content {
    display: block;
}

.dropdown:hover .dropbtn {
    background-color: #737573;
    text-decoration: none;
}

